h1 {
  font-size: 3.5rem;
  text-align: center;
  font-weight: bold;

  &::after {
    content: "";
    width: 400px;
    height: 50px;
    border: solid 3px #e8522a;
    border-color: #e8522a transparent transparent transparent;
    border-radius: 50% 50% 0 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
  }
}
